<h1 id="多账号接入" tabindex="-1"><a class="header-anchor" href="#多账号接入" aria-hidden="true">#</a> 多账号接入</h1>
<p>如果你想使用本项目接入多个公众号，在本程序中，您可以为每个帐号都设置一个 id，此 id 对应了该帐号的 appid、token 等信息。
如下表</p>
<table>
<thead>
<tr>
<th>id</th>
<th>appId</th>
<th>secret</th>
<th>其它...</th>
</tr>
</thead>
<tbody>
<tr>
<td>1</td>
<td><code>wx3cf0f39249eb0e60</code></td>
<td><code>f28f735d4f1c242f4687abb469072a29</code></td>
<td>...</td>
</tr>
<tr>
<td>2</td>
<td><code>wx49eb0e63cf0f39s2</code></td>
<td><code>8f735d4687abb469f1c2422a29f4f207</code></td>
<td>...</td>
</tr>
<tr>
<td>N</td>
<td><code>wx5cfeb0e60f392490</code></td>
<td><code>35f8f27d46f1c242f487a9072a29bb46</code></td>
<td>...</td>
</tr>
</tbody>
</table>
<p>在微信公众平台的设置中，您可以将您帐号中平台的 <code>url</code> 设置为 <code>您的网址/?id=xxx</code>，如:</p>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>http://www.easywechat.com/wechat?id=1
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>而在程序入口处，根据 <code>id</code> 查找对应帐号的 <code>appid</code> 和 其它信息来创建配置数组创建实例即可。</p>
